@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap);
/*Open Sans -> Regular(400)/Bold(700)*/

/*Montserrat -> Bold(700)*/

* {
  padding: 0;
  margin: 0;
  /*A fonte padrão é a Open Sans, pois é a mais utilizada no documento. O mesmo vale para a cor e a weight dela.*/
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #363636;
  color: var(--cinza);
  font-weight: 400;
}

body {
  background-color: #363636;
  background-color: var(--cinza);
}

html {
  font-size: 62.5%;
}

/*Variáveis*/
:root {
  --cinza: #363636;
  --verde: #63C7A9;
}

.montserrat {
  font-family: 'Montserrat', sans-serif;
}

.texto-pequeno {
  font-size: 1.225rem;
}

.maiusculo {
  text-transform: uppercase;
}

.texto-branco {
  color: white;
}

.texto-verde {
  color: #63C7A9;
  color: var(--verde);
}

.bold {
  font-weight: bold;
}

.bg-dark {
  background-color: #363636;
  background-color: var(--cinza);
}

.container {
  width: 92.5%;
  max-width: 1000px;
  margin: auto;
}

input, button, textarea {
  outline: none;
}

.erro {
  color: red;
}
/*Cabeçalho*/

header {
    width: 100%;
    height: auto;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

header img {
    height: 15vh;
    max-height: 80px;
    min-height: 65px;
}

header h1 {
    font-size: clamp(1.5rem, 2vw, 2rem);
    word-break: break-all;
}





/*Principal*/

/*Banner*/

main section.banner {
    width: 100%;
    height: 80vh;
    min-height: 500px;
    max-height: 1000px;

    background: url(/sorocabacom-teste-front-end/static/media/Background.51e90ea5.png);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;

    display: flex;
    justify-content: center;
}

main section.banner .container-img-card1 {
    width: auto;
    height: 100%;

    background: url(/sorocabacom-teste-front-end/static/media/Background.c1686559.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;

    padding: 0 15px;

    display: flex;
    flex-direction: column;
    justify-content: center;
}

main section.banner .container-img-card1 h1 {
    font-size: 1rem;
    overflow-wrap: break-word;

    padding: 10px 20px;
}

main section.banner .container-img-card1 img.red {
    height: calc(100vh - 15vh - 30vh);

    display: block;

    margin: auto;
}

main section.banner .container-img-card1 p {
    color: #F0F0F2;
    text-align: center;

    padding: 10px;
}

main section.banner .container-img-card1 div {
    display: flex;
    flex-direction: column;
    justify-content: center;

    text-align: center;

    cursor: pointer;

    position: relative;
    bottom: -1.5rem;
}

main section.banner .container-img-card1 div i.fa-mouse {
    font-size: 2rem;
    color: white;
}

main section.banner .container-img-card1 div i.fa-chevron-down {
    font-size: 1rem;
    color: white;
}

/*Sessão dos personagens*/

.personagens {
    padding: 50px 0;
}

/*Botões do carrossel*/
.personagens button {
    background: none !important;
}

.personagens .react-multiple-carousel__arrow--right {
    right: -10px !important;
}

.personagens .react-multiple-carousel__arrow--left {
    left: -10px !important;
}

/*Formulário*/

section.gradient {
    width: 100%;
    height: 50vh;
    min-height: 520px;
    max-height: 100px;
    
    background: rgb(125,237,226);
    background: linear-gradient(90deg, rgba(125,237,226,1) 0%, rgba(88,183,144,1) 100%);
}

section.gradient div.formulario {
    width: 80%;
    max-width: 600px;
    height: 110%;

    background-color: white;

    margin: auto;
    border-radius: 10px;

    display: flex;
    justify-content: center;

    position: relative;
    top: -25px;
}

section.gradient div.container-formulario {
    width: 80%;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

section.gradient h1 {
    text-align: center;
    font-size: 2rem;
    line-height: 500%;
}

section.gradient div form {
    width: 70%;
    max-width: 550px;

    margin: auto;
}

section.gradient input, textarea {
    border: solid 1px var(--cinza);

    color: var(--cinza);
}

section.gradient div form .dados {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    margin-bottom: 20px;
}

section.gradient div form .dados input {
    width: 100%;
    min-width: 120px;
}

section.gradient div form .dados div {
    width: 40%;
    min-width: 120px;
}

section.gradient div form textarea {
    width: 100%;
}

section.gradient div form button {
    width: 40%;
    min-width: 130px;

    padding: 2px 0;
    margin: 20px 0;
    border: none;

    background-color: var(--verde);

    cursor: pointer;
}

/*Media querie para o formulário, para telas menores que 598px (um tablet médio)*/

@media screen and (max-width: 598px) {
    section.gradient div form .dados {
        margin-bottom: 0;
    }

    section.gradient div form .dados div {
        width: 100%;

        margin-bottom: 20px;
    }

    section.gradient div form button {
        display: block;

        margin: 20px auto;
    }
}





/*Rodapé*/

footer {
    width: 100%;
    height: 180px;

    display: flex;
    align-items: center;
    justify-content: flex-end;
}

footer i {
    color: white;
    font-size: 4rem;

    cursor: pointer;

    transition: 0.5s;
}

footer i:hover {
    transition: 0.25s;

    text-shadow: 0 0 4px white;
}
.card {
    width: 80%;
    max-width: 250px;
    height: 400px;
    
    background-color: white;

    border-radius: 4%;
    margin: 100px auto;
}

.card .card-img {
    width: 90%;
    height: 70%;

    background-color: var(--cinza);

    margin: auto;
    border: solid 2px white;
    border-radius: 20%;

    position: relative;
    top: -50px;
}

.card .card-img img {
    height: 120%;
    width: 100%;

    display: block;

    margin: auto;
    /*Responsável por adequar a imagem ao container.*/
    margin-top: -23%;

    position: relative;
}

.card .container-texto {
    width: 80%;

    margin: auto;
}
